.TodoListItem {
    display: flex;
    align-items: center;
    padding: 1rem;
    &:nth-child(even) {
        background: #f8f9fa;
    }

    .checkbox {
        flex: 1;
        display: flex;
        align-items: center;
        cursor: pointer;
        svg { // 아이콘
            font-size: 1.5rem;
        }
        .text {
            margin-left: 0.5rem;
            flex: 1; // 차지할 수 있는 영역 모두 차지
        }

        // 체크되었을 때 보여줄 스타일
        &.checked {
            svg { // 아이콘
                color: #22b8cf;
            }
            .text {
                color: #adb5bd;
                text-decoration: line-through;
            }
        }
    }

    .remove {
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        color: #ff6b6b;
        cursor: pointer;
        &:hover {
            color: #ff8787;
        }
    }

    & + & {
        border-top: 1px solid #dee2e6;
    }
}