.TodoInsert {
    display: flex;
    background: #495057;

    input {
        background: none;
        outline: none;
        border: none;
        padding: 0.5rem;
        font-size: 1.125rem;
        line-height: 1.5;
        color: white;
        &::placeholder {
            color: #dee2e6;
        }
        flex: 1; // 버튼을 제외한 영역을 모두 차지하기
    }

    button {
        // 기본 스타일 초기화
        background: none;
        outline: none;
        border: none;
        background: #868e96;
        color: white;
        padding-left: 1rem;
        padding-right: 1rem;
        font-size: 1.5rem;
        display: flex; // <MdAdd /> 아이콘을 수직 가운데 정렬하기 위해 삽입
        align-items: center;
        cursor: pointer;
        transition: all 0.2s  ease-in;
        &:hover {
            background: #adb5bd;
        }
    }
}